import { close, warning } from 'ionicons/icons';
import { Calculators } from '../components/calc';
import DarkModeToggle from '@/components/DarkModeToggle.vue';
import '@/assets/shared.css';

export default defineComponent({
  name: 'Tab5Page',
  components: { 
    IonBackButton, 
    IonButtons, 
    IonIcon, 
    IonModal, 
    IonText, 
    IonRadioGroup, 
    IonButton, 
    IonInput, 
    IonCardContent, 
    IonCard,
    IonPage, 
    IonRadio, 
    IonLabel, 
    IonItem, 
    IonRow, 
    IonCol, 
    IonList, 
    IonGrid, 
    IonHeader, 
    IonToolbar, 
    IonTitle, 
    IonContent, 
    IonCardHeader, 
    IonCardTitle,
    DarkModeToggle 
  },
  data: () => ({
    ready: false,
    result: {
      percentage: '',
      extra: '',
      ppossum: {
        morbidity: -1,
        mortality: -1
      }
    },
    risk:{
      age: '',
      cepod: '',
      soiling: '',
      cancer: '',
      blood: '',
      number: '',
      severity: '',
      gcs: '',
      asa: '',
      gender: '',
      cardiac: '',
      respiratory: '',
      ecg: '',
      bp: '',
      hb: '',
      pulse: '',
      urea: '',
      creatinine: '',
      sodium: '',
      potassium: '',
      wcc: '',
    }
