  methods: {
    mock() {
      this.risk = {
        age: '30',
        cepod: '1',
        soiling: '1',
        cancer: '1',
        blood: '1',
        number: '1',
        severity: '4',
        gcs: '1',
        asa: '1',
        gender: '1',
        cardiac: '1',
        respiratory: '1',
        ecg: '1',
        hb: '8',
        bp: '139',
        pulse: '102',
        urea: '4.3',
        creatinine: '55',
        sodium: '134',
        potassium: '4.3',
        wcc: '5.4',
      };
    },
    reset() {
      this.result = {
        percentage: '',
        extra: '',
        ppossum: {
          morbidity: -1,
          mortality: -1
        }
      };
      this.risk = {
        age: '',
        cepod: '',
        soiling: '',
        cancer: '',
        blood: '',
        number: '',
        severity: '',
        gcs: '',
        asa: '',
        gender: '',
        cardiac: '',
        respiratory: '',
        ecg: '',
        bp: '',
        hb: '',
        pulse: '',
        urea: '',
        creatinine: '',
        sodium: '',
        potassium: '',
        wcc: '',
      };
    },
    checkParams() {
      if (this.risk.age === '') {
        return false;
      }
      if (this.risk.cepod === '') {
        return false;
      }
      if (this.risk.soiling === '') {
        return false;
      }
      if (this.risk.cancer === '') {
        return false;
      }
      if (this.risk.blood === '') {
        return false;
      }
      if (this.risk.number === '') {
        return false;
      }
      if (this.risk.severity === '') {
        return false;
      }
      if (this.risk.gcs === '') {
        return false;
      }
      if (this.risk.asa === '') {
        return false;
      }
      if (this.risk.gender === '') {
        return false;
      }
      if (this.risk.cardiac === '') {
        return false;
      }
      if (this.risk.respiratory === '') {
        return false;
      }
      if (this.risk.ecg === '') {
        return false;
      }
      if (this.risk.bp === '') {
        return false;
      }
      if (this.risk.pulse === '') {
        return false;
      }
      if (this.risk.urea === '') {
        return false;
      }
      if (this.risk.creatinine === '') {
        return false;
      }
      return true;
    },
    validateUrea() {
      // eslint-disable-next-line prefer-const
      let output = {
        erm: '',
        err: false,
      };
      if (!parseFloat(this.risk.urea) && this.risk.urea !== '' && parseFloat(this.risk.urea) !== 0) {
        output.erm = "Urea result is not a number";
        output.err = true;
        return output
      }
      if (this.risk.urea === '') {
        output.err = true;
      }
      if (parseFloat(this.risk.urea) < 0) {
        output.erm = "Urea result cannot be negative";
        output.err = true;
        return output
      }
      return output
    },
    validateCreatinine() {
      // eslint-disable-next-line prefer-const
      let output = {
        erm: '',
        err: false,
      };
      if (!parseFloat(this.risk.creatinine) && this.risk.creatinine !== '' && parseFloat(this.risk.creatinine) !== 0) {
        output.erm = "Creatinine result is not a number";
        output.err = true;
        return output
      }
      if (this.risk.creatinine === '') {
        output.err = true;
      }
      if (parseFloat(this.risk.creatinine) < 0) {
        output.erm = "Creatinine result cannot be negative";
        output.err = true;
        return output
      }
      return output
    },
    validateSodium() {
      // eslint-disable-next-line prefer-const
      let output = {
        erm: '',
        err: false,
      };
      if (!parseFloat(this.risk.sodium) && this.risk.sodium !== '' && parseFloat(this.risk.sodium) !== 0) {
        output.erm = "Sodium result is not a number";
        output.err = true;
        return output
      }
      if (this.risk.sodium === '') {
        output.err = true;
      }
      if (parseFloat(this.risk.sodium) < 0) {
        output.erm = "Sodium result cannot be negative";
        output.err = true;
        return output
      }
      return output
    },
    validatePotassium() {
      // eslint-disable-next-line prefer-const
      let output = {
        erm: '',
        err: false,
      };
      if (!parseFloat(this.risk.potassium) && this.risk.potassium !== '' && parseFloat(this.risk.potassium) !== 0) {
        output.erm = "Potassium result is not a number";
        output.err = true;
        return output
      }
      if (this.risk.potassium === '') {
        output.err = true;
      }
      if (parseFloat(this.risk.potassium) < 0) {
        output.erm = "Potassium result cannot be negative";
        output.err = true;
        return output
      }
      return output
    },
    validateWCC() {
      // eslint-disable-next-line prefer-const
      let output = {
        erm: '',
        err: false,
      };
      if (!parseFloat(this.risk.wcc) && this.risk.wcc !== '' && parseFloat(this.risk.wcc) !== 0) {
        output.erm = "White cell count is not a number";
        output.err = true;
        return output
      }
      if (this.risk.wcc === '') {
        output.err = true;
      }
      if (parseFloat(this.risk.wcc) < 0) {
        output.erm = "White cell count cannot be negative";
        output.err = true;
        return output
      }
      return output
    },
    validatePulse() {
      // eslint-disable-next-line prefer-const
      let output = {
        erm: '',
        err: false,
      };
      if (!parseInt(this.risk.pulse) && this.risk.pulse !== '' && parseInt(this.risk.pulse) !== 0) {
        output.erm = "Pulse is not a number";
        output.err = true;
        return output
      }
      if (this.risk.pulse === '') {
        output.err = true;
      }
      if (parseInt(this.risk.pulse) <= 0) {
        output.erm = "Pulse cannot be zero or negative";
        output.err = true;
        return output
      }
      return output
    },
    validateBP() {
      // eslint-disable-next-line prefer-const
      let output = {
        erm: '',
        err: false,
      };
      if (!parseInt(this.risk.bp) && this.risk.bp !== '' && parseInt(this.risk.bp) !== 0) {
        output.erm = "Blood pressure is not a number";
        output.err = true;
        return output
      }
      if (this.risk.bp === '') {
        output.err = true;
      }
      if (parseInt(this.risk.bp) <= 0) {
        output.erm = "Blood pressure cannot be zero or negative";
        output.err = true;
        return output
      }
      return output
    },
    validateAge() {
      // eslint-disable-next-line prefer-const
      let output = {
        erm: '',
        err: false,
      };
      if (!parseInt(this.risk.age) && this.risk.age !== '' && parseInt(this.risk.age) !== 0) {
        output.erm = "Age is not a number";
        output.err = true;
        return output
      }
      if (this.risk.age === '') {
        output.err = true;
      }
      // Were aged between 18 and 105 years.
      if (parseInt(this.risk.age) < 18) {
        output.erm = "The NELA calculator is not validated in patients under 18 years of age.";
        output.err = true;
      }
      if (parseInt(this.risk.age) > 105) {
        output.erm = "The NELA calculator is not validated in patients aged over 105 years.";
        output.err = true;
      }
      return output
    },
    validateASA() {
     let output = {
        erm: '',
        err: false
      }
      if (this.risk.asa === '') {
        output.err = true;
      }
      return output
    },
    validateHb() {
     let output = {
        erm: '',
        err: false
      }
      if (this.risk.hb === '') {
        output.err = true;
      }
      return output
