    validateGender() {
     let output = {
        erm: '',
        err: false
      }
      if (this.risk.gender === '') {
        output.err = true;
      }
      return output
    },
    validateCardiac() {
     let output = {
        erm: '',
        err: false
      }
      if (this.risk.cardiac === '') {
        output.err = true;
      }
      return output
    },
    validateRespiratory() {
     let output = {
        erm: '',
        err: false
      }
      if (this.risk.respiratory === '') {
        output.err = true;
      }
      return output
    },
    validateECG() {
     let output = {
        erm: '',
        err: false
      }
      if (this.risk.ecg === '') {
        output.err = true;
      }
      return output
    },
    validateGCS() {
     let output = {
        erm: '',
        err: false
      }
      if (this.risk.gcs === '') {
        output.err = true;
      }
      return output
    },
    validateSeverity() {
     let output = {
        erm: '',
        err: false
      }
      if (this.risk.severity === '') {
        output.err = true;
      }
      return output
    },
    validateNumber() {
     let output = {
        erm: '',
        err: false
      }
      if (this.risk.number === '') {
        output.err = true;
      }
      return output
    },
    validateBlood() {
     let output = {
        erm: '',
        err: false
      }
      if (this.risk.blood === '') {
        output.err = true;
      }
      return output
    },
    validateSoiling() {
     let output = {
        erm: '',
        err: false
      }
      if (this.risk.soiling === '') {
        output.err = true;
      }
      return output
    },
    validateCancer() {
     let output = {
        erm: '',
        err: false
      }
      if (this.risk.cancer === '') {
        output.err = true;
      }
      return output
    },
    validateCepod() {
     let output = {
        erm: '',
        err: false
      }
      if (this.risk.cepod === '') {
        output.err = true;
      }
      return output
    },
    showResult() {
      console.log(this.result)
      if (this.result) {
        return this.result
      }
      return { percentage: '', extra: '', ppossum: {
        morbidity: -1,
        mortality: -1,
      }}
    },
    go() {
      this.open = true;
      const nelacalc = new Calculators
      const calc = nelacalc.calcNELA(this.risk);
      const pp = nelacalc.ppcalc(this.risk);
      console.log({pp});
      if (!(calc)) {
        this.open = false;
        return;
      }
      const mortality = calc.mortality
      let exp = '';
      if (mortality < 5) {
        exp = '';
      } else {
        if (mortality < 10) {
          exp = "higher";
        } else {
          // greater than or equal to 10%
          exp = "high";
        }
      }
      this.result = {
        percentage: `${mortality}%`,
        extra: exp,
        ppossum: pp,
      };
    },
  }
});
